// paneldynamic.templateTitle: "Type in a template for dynamic panel titles. Use {panelIndex} for the panel's general position and {visiblePanelIndex} for its order among visible panels. Insert these placeholders into the pattern to add automatic numbering." => "Kirjoita malli dynaamisten paneelien otsikoille. Kyt {panelIndex} paneelin yleist sijaintia ja {visiblePanelIndex} sen jrjestyst nkyvien paneelien joukossa. Lis nm paikkamerkit kuvioon listksesi automaattisen numeroinnin."
